<template>
<div id="adsuccess">
  <head-top title="Add Device Sucess"></head-top>
  <img src="../../../assets/addevice/succeed@3x.png" alt="">
  <span>Devices successfully added</span>
  <div class="button" @click="$router.push({'path': getaddevicePath})">
    Start Immediately
  </div>
</div>
</template>
<script>
import headTop from '../../../components/common/head'
import {mapGetters} from 'vuex'

export default {
  data () {
    return {}
  },
  components: {
    headTop
  },
  computed: {
    ...mapGetters(['addevicePath']),
    getaddevicePath () {
      return this.addevicePath
    }
  },
  mounted () {
    console.log(this.getaddevicePath)
  },
  methods: {
    // go () {
    //   this.$router.push({'path': this.getaddevicePath})
    //   console.log(this.getaddevicePath)
    // }
  }
}
</script>
<style lang="less" scoped>
#adsuccess{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  display: flex;
  flex-direction: column;
  // justify-content: center;
  img{
    display: block;
    .wh(208px,208px);
    margin-top: 140px;
    align-self: center;
  }
  span{
    margin-top: 84px;
    font-size: 33px;
  }
  .button{
    margin-top: 220px;
  }
}
</style>



